import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import { useApplicationStore } from '@/store/modules/applicationModule'

const routes: Array<RouteRecordRaw> = [
  // {
  //   path: '/',
  //   name: 'home',
  //   component: HomeView
  // },

  {
    path: '/CallTop',
    name: 'CallTop',
    component: () => import(/* webpackChunkName: "CallTop" */ '@/components/container/Call/Top.vue')
  },

  {
    path: '/CallDetail',
    name: 'CallDetail',
    component: () => import(/* webpackChunkName: "CallDetail" */ '@/components/container/Call/CalllDetail.vue'),
    props: (route) => ({
       id: String(route.query.id),
      }),
  },

  {
    path: '/SentEmailTop',
    name: 'SentEmailTop',
  component: () => import(/* webpackChunkName: "SentEmailTop" */ '@/components/container/Top/index.vue')
  },

   {
    path: '/s',
    name: 's',
     component: () => import(/* webpackChunkName: "SentEmailTop2" */ '@/components/container/Top/SentEmailDetail.vue'),
    props: (route) => ({
        imgUrl: String(route.query.imgUrl),
        title: String(route.query.title),
        text: String(route.query.text),
      }),
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})


router.beforeEach((to, from, next) => {

   const ApplicationStore = useApplicationStore()


      ApplicationStore.isLoadComplete = false

  console.log("beforeach");


next();
});

router.afterEach((to, from) => {
  const ApplicationStore = useApplicationStore()


      ApplicationStore.isLoadComplete = true

  console.log("aftereach");

});

export default router
